$bg: #161d26;
$bg-color: #1f2935;
$color: #95a5c6;
$primary-color: #0abead;
$secondary-color: #f57f17;
$white: #ffffff;

*,
*::before,
*::after {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100vh;
  background-color: $bg;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  background-color: $bg-color;
}
body::-webkit-scrollbar-thumb {
  background-color: $bg;
}
.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: $bg-color;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.2);
}
.nav {
  width: 100%;
  max-width: 1360px;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0em 1em;
}
.brand-name {
  font-size: 25px;
  color: $primary-color;
  letter-spacing: 2px;
  text-decoration: none;
}
.mobile-menu {
  font-size: 30px;
  font-weight: 600;
  color: $primary-color;
  letter-spacing: 2px;
  cursor: pointer;
}
.desktop-nav {
  display: none;
  justify-content: space-between;
}
.nav-link {
  font-size: 15px;
  text-decoration: none;
  background-color: $bg-color;
  color: darken($color, 10%);
}
.nav-link:hover {
  color: $primary-color;
}
.mobile-nav {
  display: none;
  flex-direction: column;
  width: 100%;
  padding: 1em auto;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.2);
}
.mobile-nav-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 6vh;
  font-size: 15px;
  text-decoration: none;
  background-color: $bg-color;
  color: $color;
}
.mobile-nav-link:hover {
  background-color: $primary-color;
  color: $bg-color;
}
.main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}
.section {
  width: 100%;
  max-width: 1360px;
  padding: 0em 2em;
  min-height: 750px;
  .section-header {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 70px;
  }
  .header-name {
    font-size: 25px;
    font-weight: 600;
    color: $primary-color;
  }
  .header-line {
    font-size: 15px;
    color: $color;
  }
  .line {
    width: 300px;
    height: 4px;
    border-radius: 5px;
    background-color: $primary-color;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .bold-line {
    width: 100px;
    height: 8px;
    border-radius: 10px;
    background-color: $primary-color;
  }
}
#jumbotron {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .about_me {
    font-size: 18px;
    text-transform: uppercase;
    color: $primary-color;
  }
  .about_me span {
    color: $secondary-color;
    font-size: 22px;
  }
  .slogan {
    font-size: 32px;
    font-weight: 600;
    line-height: 45px;
    color: $primary-color;
    letter-spacing: 3px;
  }
  .info {
    font-size: 18px;
    display: block;
    color: $color;
  }
  .cta {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    width: 330px;
  }
  .cta a {
    padding: 0.5em;
    border-radius: 50px;
    width: 150px;
    font-size: 18px;
    font-weight: 600;
    text-align: center;
  }
  .primary-cta {
    background-color: $primary-color;
    color: #ffffff;
  }
  .primary-cta:hover {
    background-color: darken($primary-color, 20%);
  }
  .secondary-cta {
    background-color: $secondary-color;
    color: #ffffff;
  }
  .secondary-cta:hover {
    background-color: darken($secondary-color, 20%);
  }
  .img-wrapper {
    border-radius: 1000px;
    width: 342px;
    height: 440px;
    border: 20px solid $bg-color;
  }
  img {
    border-radius: 1000px;
    width: 300px;
  }
}
#about {
  .about-content {
    width: 100%;
    height: 550px;
    display: flex;
    justify-content: space-between;
  }
  .about-pic {
    width: 450px;
  }
  .about {
    width: 58%;
  }
  .about-heading {
    font-size: 30px;
    font-weight: 600;
    color: $primary-color;
  }
  .about-info {
    font-size: 18px;
    color: $color;
    line-height: 25px;
    text-align: justify;
  }
  .skill-set-heading {
    font-size: 22px;
    color: $primary-color;
  }
  .skill {
    font-size: 18px;
    line-height: 35px;
    color: $color;
    list-style: disc;
  }
}
#about .wapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 700px;
}
#resume {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .resume-content {
    width: 100%;
    height: 650px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .resume-sidebar {
    width: 400px;
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: $bg-color;
    padding-right: 1em;
    padding-top: 1em;
    padding-bottom: 1em;
    border-left: 30px solid $primary-color;
  }
  .section-switch {
    background-color: $bg-color;
    color: $color;
    width: fit-content;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    padding-right: 1em;
    padding-top: 0.6em;
    padding-bottom: 0.6em;
  }
  .section-switch .switch-icon {
    padding: 1em;
    color: $primary-color;
    font-size: 12px;
    background-color: $primary-color;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    margin-right: 1em;
  }
  .section-switch:hover {
    background-color: $primary-color;
    border-radius: 50px;
    color: $white;
  }
  .resume-sections {
    width: 800px;
    height: 500px;
    overflow: hidden;
    overflow-y: scroll;
    padding: 0em 2em;
    border-left: 3px solid $primary-color;
  }
  .resume-sections::-webkit-scrollbar {
    background-color: $bg-color;
    box-shadow: 0px 3px 6px rgb(0, 0, 0, 0.7);
  }
  .resume-sections::-webkit-scrollbar-thumb {
    background-color: $bg;
  }
  .resume-section {
    width: 100%;
    height: 500px;
  }
  .resume-card {
    padding: 1em 0em;
    display: flex;
    position: relative;
  }
  .dot-card {
    width: 32px;
    height: 22px;
    background-color: $primary-color;
    position: absolute;
    left: -50px;
    top: 15px;
    border-radius: 50px;
  }
  .card-col {
    width: 85%;
  }
  .resume-card-heading {
    font-size: 20px;
    font-weight: 600;
    color: $secondary-color;
    margin-bottom: 0.5em;
  }
  .resume-card-info {
    color: $color;
    font-size: 18px;
  }
  .resume-card-explain {
    font-size: 17px;
    color: $color;
  }
  .date {
    padding: 0.5em 0.9em;
    background-color: $secondary-color;
    font-size: 16px;
    border-radius: 50px;
    width: fit-content;
    height: fit-content;
    color: $white;
  }
}
#services {
  .services-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 550px;
    margin-top: 50px;
  }
  .services-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .services-card {
    width: 390px;
    height: 250px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 2em;
  }
  .services-card .icon {
    width: 55px;
    fill: #ffffff;
  }
  .services-card .card-heading {
    font-size: 22px;
    font-weight: 600;
    color: white;
  }
  .services-card .card-info {
    font-size: 16px;
    color: white;
    text-align: center;
  }
  .card-1 {
    background-color: #8540ff;
  }
  .card-2 {
    background-color: #ffc50c;
  }
  .card-3 {
    background-color: #ec407a;
  }
  .card-4 {
    background-color: #ff6331;
  }
  .card-5 {
    background-color: #13b6ff;
  }
  .card-6 {
    background-color: #49af46;
  }
  .card-1:hover {
    background-color: darken(#8540ff, 10%);
  }
  .card-2:hover {
    background-color: darken(#ffc50c, 10%);
  }
  .card-3:hover {
    background-color: darken(#ec407a, 10%);
  }
  .card-4:hover {
    background-color: darken(#ff6331, 10%);
  }
  .card-5:hover {
    background-color: darken(#13b6ff, 10%);
  }
  .card-6:hover {
    background-color: darken(#49af46, 10%);
  }
}
#portfolio {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  .portfolio-content {
    width: 1300px;
    overflow: hidden;
    overflow-x: scroll;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .portfolio-content::-webkit-scrollbar {
    background-color: $bg-color;
    box-shadow: 0px 3px 6px rgb(0, 0, 0, 0.5);
  }
  .portfolio-content::-webkit-scrollbar-thumb {
    background-color: $bg;
  }
  .portfolio-pic {
    height: 550px;
    margin-right: 2em;
    margin-bottom: 2em;
  }
}
#pricing {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .pricing-content {
    width: 100%;
    height: 700px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .pricing-card {
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $bg-color;
    box-shadow: 0px 3px 6px rgb(0, 0, 0, 0.3);
  }
  .card-head {
    padding: 2em;
    width: 100%;
  }
  .head-1 {
    background-color: #ff6331;
  }
  .head-2 {
    background-color: #803afb;
  }
  .head-3 {
    background-color: #f5276c;
  }
  .pricing-tear {
    font-size: 30px;
    text-align: center;
    font-weight: 600;
    color: $white;
  }
  .price {
    font-size: 22px;
    text-align: center;
    padding-top: 0.5em;
    color: $white;
  }
  .price span {
    font-size: 18px;
  }
  .tear-features {
    list-style: none;
    width: 100%;
    margin-top: 2em;
  }
  .feature-active {
    font-size: 20px;
    color: $color;
    padding: 0.5em;
  }
  .feature-active span {
    padding: 1em;
    color: #49af46;
  }
  .feature-inactive {
    font-size: 20px;
    color: $color;
    padding: 0.5em;
  }
  .feature-inactive span {
    padding: 1em;
    color: #f5276c;
  }
  .card-cta {
    padding: 0.9em 1em;
    border-radius: 50px;
    border: none;
    color: $white;
    font-size: 18px;
    margin: 1.5em;
    cursor: pointer;
  }
  .card-cta:hover {
    box-shadow: 0px 3px 6px rgb(0, 0, 0, 0.7);
  }
}
#contact {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
  .map {
    width: 100vw;
    max-width: 1600px;
    height: 650px;
  }
  .form {
    position: absolute;
    bottom: 70px;
    right: 120px;
    width: 400px;
    height: 500px;
    background: $bg-color;
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 2em 2.5em;
  }
  .form-heading {
    font-size: 30px;
    color: $primary-color;
    font-weight: 600;
  }
  .form-input {
    padding: 0.7em 1em;
    background-color: $bg;
    border: 1px solid $primary-color;
    border-radius: 3px;
  }
  .form-input:focus {
    outline: 2px solid $primary-color;
  }
  .form-textarea {
    height: 100px;
  }
  .form-cta {
    padding: 0.4em;
    font-size: 18px;
    background-color: $primary-color;
    border: none;
    border-radius: 3px;
    color: $bg;
    cursor: pointer;
    transition: 100ms;
  }
  .form-cta:hover,
  :focus {
    background-color: $color;
  }
}
.back-to-top {
  position: absolute;
  bottom: 30px;
  right: 70px;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $primary-color;
  border-radius: 50px;
}
.back-to-top-svg {
  width: 30px;
  color: white;
}
.back-to-top:hover {
  box-shadow: 0px 3px 6px rgb(0, 0, 0, 0.7);
}
.footer {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: $color;
}
@media (min-width: 660px) {
  .nav {
    padding: 0em 2em;
  }
  .desktop-nav {
    display: flex;
    width: 60%;
  }
  .mobile-menu {
    display: none;
  }
}
@media (min-width: 930px) {
  .desktop-nav {
    width: 55%;
  }
}
@media (min-width: 1060px) {
  .desktop-nav {
    width: 50%;
  }
}
